import logo from './logo.svg';
import './App.css';
import { WebR } from 'webr';
import { useEffect, useState } from 'react';

const webR = new WebR();
await webR.init();
await webR.installPackages(["tidyverse"])
const categoryNames = await webR.evalRRaw(
  `library(tidyverse)
dataCT <- read.csv("${window.location.href}/ct_data.csv")
names(dataCT) <- lapply(dataCT[1,], as.character)
dataCT <- dataCT[-1,]
dataCT[,3:323] <- sapply(dataCT[,3:323], as.numeric)
selectDataCT <- dataCT[,c(1,2,183,186,207,210,231,74:77,248:253,255,256,275:290,295:308,310:317,321,322)]
print(names(selectDataCT))`,
"string[]"
)

async function getAPlot(x,y) {
  const shelter = await new webR.Shelter();
  const capture = await shelter.captureR(
    `myPlot <- ggplot(selectDataCT, mapping = aes(x = \`${x}\`, y = \`${y}\`)) +
      geom_point()
    print(myPlot)`, {
    captureGraphics: {
      width: 300,
      height: 150
    }
});
  return capture.images
}

function App() {
  const [xAxis, setXAxis] = useState()
  const [yAxis, setYAxis] = useState()
  const [result, setResult] = useState()
  const [loading, setLoading] = useState(false)

  function getResults() {
    setLoading(true);
    getAPlot(xAxis, yAxis).then(r => {
      setResult(r);
      setLoading(false)
  });
  }

  useEffect(() => {
    if (result) {
      
      document.getElementById("plot-canvas").getContext("2d").drawImage(result[0], 0, 0)
    }
  }, [result])

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Let's make a graph! 
          <br/>
            <select name="xAxis" value={xAxis} onChange={e => setXAxis(e.target.value)}>
              <option value={null}></option>
              {categoryNames.slice(2).map(name => <option value={name}>{name}</option>)}
            </select>
          <br/>
            <select name="yAxis" value={yAxis} onChange={e => setYAxis(e.target.value)}>
              <option value={null}></option>
              {categoryNames.slice(2).map(name => <option value={name}>{name}</option>)}
            </select>
          <br/>
          <button onClick={getResults} disabled={loading || !xAxis || !yAxis}>
            Get a plot!
          </button>
        </p>
        <canvas id="plot-canvas" width="600" height="300" style={{border: "solid white"}}/>
      </header>
    </div>
  );
}

export default App;
